<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()">
          <span>返回</span>
        </div>
        <span>修改群名称</span>
        <div style="float:right;margin-right:5px;">
          <van-button type="primary" size="small" @click="confirm">完成</van-button>
        </div>
      </div>
    </header>
    <div style="padding:20px 0 10px 10px;color:#737475">群聊名称</div>
    <van-cell-group>
      <van-field v-model="value" placeholder="请输入群聊名" />
    </van-cell-group>
  </div>
</template>
<script>
import { getMessage, send } from "../../socket/socket";
import qs from "qs";
export default {
  data() {
    return {
      value: this.$route.query.groupName,
      userInfo: ""
    };
  },
  mounted() {
    this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
  },
  methods: {
    confirm() {
      let params = {
        from: this.userInfo.userId,
        roomId: this.$route.query.roomId,
        dataGroup: "1",
        dataType: "10",
        msg: this.value
      };
      send(params);
      this.$router.back();
    }
  }
};
</script>
<style>
</style>


